import React, { useState } from 'react';
import {
    TextareaAutosize,
    TextField,
    Button,
    Typography,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Grid
} from '@mui/material';


const GoWasmComponent: React.FC = () => {
    const [input, setInput] = useState("");
    const [unescape, setUnescape] = useState(0);
    const [indent, setIndent] = useState(4);
    const [output, setOutput] = useState("");

    const handleButtonClick = () => {
        const formatted = window.functions.formatJSON(JSON.stringify({
            text: input,
            unescape: unescape,
            indent: indent
        }));
        setOutput(formatted);
    }

    return (
        <Grid container spacing={2}>
            <Grid item xs={5}>
                <Typography variant="h6" gutterBottom>Input JSON:</Typography>
                <TextareaAutosize
                    minRows={50}
                    maxRows={100}
                    style={{ width: '100%', maxWidth: '100%', padding: '8px', border: '1px solid #ccc', borderRadius: '4px' }}
                    value={input}
                    onChange={e => setInput(e.target.value)}
                />
            </Grid>
            <Grid item xs={1}>
                <FormControl sx={{ m: 1, minWidth: 120 }}>
                    <InputLabel>Unescape Quotes</InputLabel>
                    <Select
                        value={unescape}
                        label="Unescape Quotes"
                        onChange={e => setUnescape(+e.target.value)}
                        style={{ marginBottom: '8px' }}
                    >
                        <MenuItem value={0}>
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={1}>1</MenuItem>
                        <MenuItem value={2}>2</MenuItem>
                        <MenuItem value={3}>3</MenuItem>
                    </Select>
                    <TextField
                        variant="outlined"
                        type="number"
                        label="Indent"
                        value={indent}
                        onChange={e => setIndent(parseInt(e.target.value))}
                    />
                    <Button variant="contained" color="primary" onClick={handleButtonClick}>
                        Format
                    </Button>
                </FormControl>
            </Grid>
            <Grid item xs={5}>
                <Typography variant="h6" gutterBottom>Output:</Typography>
                <TextareaAutosize
                    minRows={50}
                    maxRows={100}
                    style={{ width: '100%', maxWidth: '100%', padding: '8px', border: '1px solid #ccc', borderRadius: '4px' }}
                    value={output}
                />
            </Grid>
        </Grid>
    );
}

export default GoWasmComponent;